.main {
  background: rgba(68, 0, 255, 0.1);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.resume {
  width: 210mm;
  height: 297mm;
  border: 1px solid black;
  box-sizing: border-box;
  margin: 0 auto;
  padding: 20px;
}

@media (max-width: 767px) {
  /* CSS for mobile devices */
  .a4-box {
    width: calc(100% - 40px);
    height: auto;
  }
}

.pfp {
  display: flex;
  justify-content: center;
}

.left {
  background-color: #272928;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 30px;
  color: white;
}

.right {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 20px;
  margin-top: 50px;
  align-items: flex-start;
}

.leftTitle {
  color: #44b2f4;
}

.line {
  display: flex;
}
